<template>
  <div id="app">
    <nav class="navbar navbar-dark bg-dark navbar-expand-lg navbar-light bg-light">
      <div class="container">
        <button
          class="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarTogglerDemo01"
          aria-controls="navbarTogglerDemo01"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse" id="navbarTogglerDemo01">
          <a class="navbar-brand" href="#">RUDAN</a>
          <ul class="navbar-nav mr-auto mt-2 mt-lg-0">
            <!-- <li class="nav-item active">
              <a class="nav-link" href="#">
                Home
                <span class="sr-only">(current)</span>
              </a>
            </li>
            <li class="nav-item my-2 my-lg-0 mr-sm-2">
              <a class="nav-link" href="#">Link</a>
            </li>-->
          </ul>
        </div>
      </div>
    </nav>
    <br />
    <br />
    <div class="container">
      <h2>Mquina 1</h2>
      <hr>
      <div class="row">
        <div class="col-6">
          <h3>Balana 1</h3>
          <div>
            <div class="row">
              <div class="col-3">Nome Lote</div>
              <div class="col-6">
                <div class="input-group">
                  <input
                    :disabled="editar_lote_1"
                    type="text"
                    class="form-control"
                    placeholder="Nome lote"
                    value="Lote 00111"
                  />
                </div>
              </div>
              <div class="col-3">
                <div class="btn btn-primary" @click=" EditarLote(1)">{{texto_lote_1}}</div>
              </div>
            </div>
          </div>
          <hr />
          <div>
            <div class="row">
              <div class="col-3">Peso Programado</div>
              <div class="col-6">
                <div class="input-group">
                  <input
                    :disabled="editar_peso_1"
                    type="text"
                    class="form-control"
                    placeholder="peso programado"
                    value="2000"
                  />
                </div>
              </div>
              <div class="col-3">
                <div class="btn btn-primary" @click=" EditarPeso(1)">{{texto_peso_1}}</div>
              </div>
            </div>
          </div>
          <hr />
          <div>
            <div class="row">
              <div class="col-3">Peso Atual</div>
              <div class="col-6">
                <div class="input-group">
                  <input
                    disabled
                    type="text"
                    class="form-control leitura-peso"
                    placeholder="peso atual"
                    value="1500"
                  />
                </div>
              </div>
              <div class="col-3"></div>
            </div>
          </div>
          <hr />
          <div>
            <div class="row">
              <div class="col-3">Ultimo Peso</div>
              <div class="col-6">
                <div class="input-group">
                  <input
                    disabled
                    type="text"
                    class="form-control leitura-peso"
                    placeholder="ultimo peso "
                    value="2000"
                  />
                </div>
              </div>
              <div class="col-3"></div>
            </div>
          </div>
        </div>
        <div class="col-6">
          <h3>Balana 2</h3>
          <div>
            <div class="row">
              <div class="col-3">Nome Lote</div>
              <div class="col-6">
                <div class="input-group">
                  <input
                    :disabled="editar_lote_2"
                    type="text"
                    class="form-control"
                    placeholder="Nome lote"
                    value="Lote 00111"
                  />
                </div>
              </div>
              <div class="col-3">
                <div class="btn btn-primary" @click="EditarLote(2)">{{texto_lote_2}}</div>
              </div>
            </div>
          </div>
          <hr />
          <div>
            <div class="row">
              <div class="col-3">Peso Programado</div>
              <div class="col-6">
                <div class="input-group">
                  <input
                    :disabled="editar_peso_2"
                    type="text"
                    class="form-control"
                    placeholder="peso programado"
                    value="2000"
                  />
                </div>
              </div>
              <div class="col-3">
                <div class="btn btn-primary" @click="EditarPeso(2)">{{texto_peso_2}}</div>
              </div>
            </div>
          </div>
          <hr />
          <div>
            <div class="row">
              <div class="col-3">Peso Atual</div>
              <div class="col-6">
                <div class="input-group">
                  <input
                    disabled
                    type="text"
                    class="form-control leitura-peso"
                    placeholder="peso atual"
                    value="1500"
                  />
                </div>
              </div>
              <div class="col-3"></div>
            </div>
          </div>
          <hr />
          <div>
            <div class="row">
              <div class="col-3">Ultimo Peso</div>
              <div class="col-6">
                <div class="input-group">
                  <input
                    disabled
                    type="text"
                    class="form-control leitura-peso"
                    placeholder="ultimo peso "
                    value="2000"
                  />
                </div>
              </div>
              <div class="col-3"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <br />
    <br />
    <br />
    <!-- tabela -->
    <div class="container">
      <h4>Histrio de Peso</h4>
      <table class="table table-hover">
        <thead>
          <tr>
            <th scope="col">#</th>
            <th scope="col">Balana</th>
            <th scope="col">Nome Lote</th>
            <th scope="col">Peso Programado</th>
            <th scope="col">Ultimo Peso</th>
            <th scope="col">Data</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <th scope="row">1</th>
            <td>Balana 1</td>
            <td>Lote 00111</td>
            <td>2000</td>
            <td>2000</td>
            <td>15:33 / 02-09-20</td>
          </tr>
          <tr>
            <th scope="row">2</th>
            <td>Balana 2</td>
            <td>Lote 00111</td>
            <td>2000</td>
            <td>2000</td>
            <td>15:33 / 02-09-20</td>
          </tr>
          <tr>
            <th scope="row">3</th>
            <td>Balana 1</td>
            <td>Lote 00111</td>
            <td>2000</td>
            <td>2000</td>
            <td>15:33 / 02-09-20</td>
          </tr>
          <tr>
            <th scope="row">4</th>
            <td>Balana 2</td>
            <td>Lote 00111</td>
            <td>2000</td>
            <td>2000</td>
            <td>15:33 / 02-09-20</td>
          </tr>
        </tbody>
      </table>
      <hr>
      <br>
    </div>
    <div class="container">
      <h4>Exemplo Grfico</h4>
      <hr>
      
      <apexchart type="line" height="350" :options="chartOptions" :series="series" />
    </div>
  </div>
</template>

<script>
export default {
  name: "app",
  data() {
    return {
      msg: "Welcome to Your Vue.js App",
      editar_lote_1: true,
      editar_lote_2: true,
      editar_peso_1: true,
      editar_peso_2: true,
      series: [
        {
          name: "Produo",
          data: [999, 500, 600, 800, 300, 600, 800, 750, 950, 800, 750, 950]
        }
        
      ],
      chartOptions: {
        chart: {
          height: 350,
          zoom: {
            enabled: false
          },
        },
          colors: ['#77B6EA', '#545454'],
        dataLabels: {
          enabled: false
        },
        tooltip: {
          marker: {
            colors: "#40a396"
          }
        },
        stroke: {
          curve: "straight",
          colors: "#40a396"
        },
        title: {
          text: "Produo por ms",
          align: "left"
        },
        markers: {
          size: [0],
          colors: "#40a396"
        },
        grid: {
          row: {
            colors: ["#d6d6d6", "transparent"], // takes an array which will be repeated on columns
            opacity: 0.5
          }
        },
        xaxis: {
          categories: [
            "Jan",
            "Fev",
            "Mar",
            "Abr",
            "Mai",
            "Jun",
            "Jul",
            "Ago",
            "Set",
            "Out",
            "Nov",
            "Dez"
          ]
        }
      }
    };
  },
  computed: {
    texto_lote_1() {
      if (this.editar_lote_1) {
        return "Editar";
      }
      return "Salvar";
    },
    texto_peso_1() {
      if (this.editar_peso_1) {
        return "Editar";
      }
      return "Salvar";
    },
    texto_lote_2() {
      if (this.editar_lote_2) {
        return "Editar";
      }
      return "Salvar";
    },
    texto_peso_2() {
      if (this.editar_peso_2) {
        return "Editar";
      }
      return "Salvar";
    },
  },
  methods: {
    EditarLote(id) {
      if (id == 1) {
        this.editar_lote_1 = !this.editar_lote_1;
      }
      if (id == 2) {
        this.editar_lote_2 = !this.editar_lote_2;
      }
    },
    EditarPeso(id) {
      if (id == 1) {
        this.editar_peso_1 = !this.editar_peso_1;
      }
      if (id == 2) {
        this.editar_peso_2 = !this.editar_peso_2;
      }
    },
  },
};
</script>

<style>
.leitura-peso {
  color: blue;
  font-weight: 500;
}
</style>



